<template>
  <!-- instruments-section -->
  <section v-if="!detail" class="instruments-section">
    <div class="inner-banner demo-2 text-center">
      <div id="breadcrumb_wrapper">
        <div class="container">
          <h2>Faculty & Staff Members</h2>
          <h6></h6>
        </div>
      </div>
    </div>
    <!--- instruments ---->
    <div class="instruments">
      <div class="container">
        <div class="row">
          <h3 class="last-updated"> 
            <small> Mentor System</small>
          </h3>
          <p>
            <strong></strong>
            
          </p>
          <p>
            The mentor system is the core of the Pioneer School. Each pioneer student has an mentor to help the
            students solve various problems in the study and life, and to carry out personalized learning
            guidance.
            The mentor plays two roles: First, the mirror, in the constant communication with the students, let
            the students see themselves, and then move away the "stone" that is in the way.
            The second is the catalyst, as we all in the learning community, the role of the mentor as a
            catalyst, and the promotion of the learning community to further study on certain topics.
          </p>
          <!--Horizontal Tab-->
        </div>
        <div class="row" style="margin-top: 30px;">
          <div class="lab-instruments" id="detail">
            <div>
              <h3 class="last-updated"> 
                <small> Faculty & Staff</small>
              </h3>
              <div class="lab-instruments">
                <el-tabs v-model="activeName" type="border-card">
                  <el-tab-pane label=" ALL" name="first">
                    <div>
                      <div v-for="(item, index) in staffList" :key="item.id">
                        <div v-if="item.type == 'f'" class="col-md-3 jm-item first">
                          <div class="jm-item-wrapper">
                            <div class="jm-item-image">
                              <img v-if="item.id != 'lxw'"
                                   :src="basePath + '/static/images/organization/' + item.id +'_1.jpg'"
                                   :alt="item.name">
                              <img v-else :src="basePath + '/static/images/organization/' + item.id +'_2.jpg'"
                                   :alt="item.name">
                              <span class="jm-item-overlay"> </span>
                              <div class="jm-item-button"><a
                                @click="doClick(item)" style="cursor:pointer;">View</a></div>
                            </div>
                            <div class="jm-item-title">{{item.name}} {{item.enName}}</div>
                          </div>
                        </div>
                      </div>
                      <div class="clearfix"></div>
                    </div>
                  </el-tab-pane>
                  <el-tab-pane label=" Mentor" name="second">
                    <div>
                      <div v-for="(item, index) in staffList" :key="item.id">
                        <div v-if="item.category.indexOf('1')!=-1 && item.type == 'f'">
                          <div class="col-md-3 jm-item first">
                            <div class="jm-item-wrapper">
                              <div class="jm-item-image">
                                <img v-if="item.id != 'lxw'"
                                     :src="basePath + '/static/images/organization/' + item.id +'_1.jpg'"
                                     :alt="item.name">
                                <img v-else :src="basePath + '/static/images/organization/' + item.id +'_2.jpg'"
                                     :alt="item.name">
                                <span class="jm-item-overlay"> </span>
                                <div class="jm-item-button"><a
                                  @click="doClick(item)" style="cursor:pointer;">View</a>
                                </div>
                              </div>
                              <div class="jm-item-title">{{item.name}} {{item.enName}}</div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div class="clearfix"></div>
                    </div>
                  </el-tab-pane>
                  <el-tab-pane label=" Assistant Teacher" name="third">
                    <div>
                      <div v-for="(item, index) in staffList" :key="item.id">
                        <div v-if="item.category.indexOf('2')!=-1 && item.type == 'f'">
                          <div class="col-md-3 jm-item first">
                            <div class="jm-item-wrapper">
                              <div class="jm-item-image">
                                <img v-if="item.id != 'lxw'"
                                     :src="basePath + '/static/images/organization/' + item.id +'_1.jpg'"
                                     :alt="item.name">
                                <img v-else :src="basePath + '/static/images/organization/' + item.id +'_2.jpg'"
                                     :alt="item.name">
                                <span class="jm-item-overlay"> </span>
                                <div class="jm-item-button"><a
                                  @click="doClick(item)" style="cursor:pointer;">View</a>
                                </div>
                              </div>
                              <div class="jm-item-title">{{item.name}} {{item.enName}}</div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div class="clearfix"></div>
                    </div>
                  </el-tab-pane>
                  <el-tab-pane label=" Full-time Staff" name="fourth">
                    <div>
                      <div v-for="(item, index) in staffList" :key="item.id">
                        <div v-if="item.category.indexOf('7')!=-1 && item.type == 'f'">
                          <div class="col-md-3 jm-item first">
                            <div class="jm-item-wrapper">
                              <div class="jm-item-image">
                                <img v-if="item.id != 'lxw'"
                                     :src="basePath + '/static/images/organization/' + item.id +'_1.jpg'"
                                     :alt="item.name">
                                <img v-else :src="basePath + '/static/images/organization/' + item.id +'_2.jpg'"
                                     :alt="item.name">
                                <span class="jm-item-overlay"> </span>
                                <div class="jm-item-button"><a
                                  @click="doClick(item)" style="cursor:pointer;">View</a>
                                </div>
                              </div>
                              <div class="jm-item-title">{{item.name}} {{item.enName}}</div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div class="clearfix"></div>
                    </div>
                  </el-tab-pane>
                  <el-tab-pane label=" Part-time Staff" name="fifth">
                    <div>
                      <div v-for="(item, index) in staffList" :key="item.id">
                        <div v-if="item.category.indexOf('8')!=-1 && item.type == 'f'">
                          <div class="col-md-3 jm-item first">
                            <div class="jm-item-wrapper">
                              <div class="jm-item-image">
                                <img v-if="item.id != 'lxw'"
                                     :src="basePath + '/static/images/organization/' + item.id +'_1.jpg'"
                                     :alt="item.name">
                                <img v-else :src="basePath + '/static/images/organization/' + item.id +'_2.jpg'"
                                     :alt="item.name">
                                <span class="jm-item-overlay"> </span>
                                <div class="jm-item-button"><a
                                  @click="doClick(item)" style="cursor:pointer;">View</a>
                                </div>
                              </div>
                              <div class="jm-item-title">{{item.name}} {{item.enName}}</div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div class="clearfix"></div>
                    </div>
                  </el-tab-pane>
                  <el-tab-pane label=" Volunteers" name="sixth">
                    <div>
                      <div v-for="(item, index) in staffList" :key="item.id">
                        <div v-if="item.category.indexOf('4')!=-1 && item.type == 'f'">
                          <div class="col-md-3 jm-item first">
                            <div class="jm-item-wrapper">
                              <div class="jm-item-image">
                                <img v-if="item.id != 'lxw'"
                                     :src="basePath + '/static/images/organization/' + item.id +'_1.jpg'"
                                     :alt="item.name">
                                <img v-else :src="basePath + '/static/images/organization/' + item.id +'_2.jpg'"
                                     :alt="item.name">
                                <span class="jm-item-overlay"> </span>
                                <div class="jm-item-button"><a
                                  @click="doClick(item)" style="cursor:pointer;">View</a>
                                </div>
                              </div>
                              <div class="jm-item-title">{{item.name}} {{item.enName}}</div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div class="clearfix"></div>
                    </div>
                  </el-tab-pane>
                </el-tabs>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--- /instruments ---->
  </section>
  <!-- //instruments-section -->

  <CPersonnel v-else :item="user"/>
</template>

<script>
  import axios from 'axios';
  import CPersonnel from "../../components/Personnel";

  export default {
    name: 'faculty_staff',
    components: {CPersonnel},
    data() {
      return {
        basePath: basePath,
        user: '',
        activeName: 'first',
        staffList: '',
        detail: false,
      }
    },
    mounted() {
      axios.get(basePath + '/staff').then(response => {
        this.staffList = response.data;
      })
    },
    methods: {
      doClick(item) {
        let speed = 10;
        let timer = setInterval(function () {
          this.scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
          if (this.scrollTop > 0) {
            this.scrollTop = (this.scrollTop - speed > 0) ? (this.scrollTop - speed) : 0;
            speed += 20;
            window.scrollTo(0, this.scrollTop);
          } else {
            clearInterval(timer);
          }
        }, 16)
        this.user = item
        this.detail = true
      }
    }
  }
</script>



