<template>

  <!-- instruments-section -->
  <section v-if="!detail" class="laboratory">
    <div class="inner-banner demo-2 text-center">
      <div id="breadcrumb_wrapper">
        <div class="container">
          <h2>Universities</h2>
          <h6></h6>
        </div>
      </div>
    </div>
    <!--- instruments ---->
    <div class="technologies">
      <div class="container">
        <div class="row">
          <h3 class="last-updated">
            <small>Introduction to Liberal Arts College</small>
          </h3>

          <div class="tech-accordian">
            <div class="panel-group" id="accordion" role="tablist" aria-multiselectable="true">
              <div class="panel panel-default">
                <div class="panel-heading" role="tab" id="headingOne">
                  <h4 class="panel-title asd">
                    <span>&#8594;</span>
                    <a class="pa_italic" role="button" data-toggle="collapse" data-parent="#accordion"
                       href="#collapseOne" aria-expanded="true" aria-controls="collapseOne">

                      <label class="horse1"> The Chinese view of Liberal Arts
                        College</label>
                    </a>
                  </h4>
                </div>
                <div id="collapseOne" class="panel-collapse collapse in" role="tabpanel"
                     aria-labelledby="headingOne">
                  <div class="panel-body panel_text">
                    <div class="row">
                      <div class="col-md-5">
                        <img :src="basePath + '/static/images/universities/SJU_1.jpg'"/>
                        <p class="diff">
                          <small> @Brace Hemmelgarn bracehemmelgarn 21 Sep 2016<br></small>
                          <br> College of Saint Benedict|Saint John's University<br>

                        </p>
                      </div>
                      <div class="col-md-1"></div>
                      <div class="col-md-6">
                        <p><strong>Liberal Arts
                          CollegeLAC</strong>
                        </p>
                        <p></p>
                        <p>
                          </p>
                        <p></p>
                        <p>
                          </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="panel panel-default">
                <div class="panel-heading" role="tab" id="headingTwo">
                  <h4 class="panel-title asd">
                    <span>&#8594;</span>
                    <a class="pa_italic collapsed" role="button" data-toggle="collapse"
                       data-parent="#accordion" href="#collapseTwo" aria-expanded="false"
                       aria-controls="collapseTwo">
                      <label class="horse2"> Advantages of LACs</label>
                    </a>
                  </h4>
                </div>
                <div id="collapseTwo" class="panel-collapse collapse" role="tabpanel"
                     aria-labelledby="headingTwo">
                  <div class="panel-body panel_text">
                    <div class="ins-details">
                      <div class="span span1">
                        <p class="left"> Focus On Education</p>
                        <p class="right">
                          </p>
                        <div class="clearfix"></div>
                      </div>
                      <div class="span span2">
                        <p class="left"> Small Scale</p>
                        <p class="right">
                          
                          Williams
                          College Wellesley
                          College</p>
                        <div class="clearfix"></div>
                      </div>
                      <div class="span span1">
                        <p class="left"> Best Quality</p>
                        <p class="right">
                          
                          3%
                          CEO8%
                          19%
                          20%
                          23%
                        </p>

                        <div class="clearfix"></div>
                      </div>
                      <div class="span span2">
                        <p class="left"> Promising Future</p>
                        <p class="right">
                          TopLAC36Occidental
                          College
                        </p>
                        <div class="clearfix"></div>
                      </div>
                      <div class="span span1">
                        <p class="left"> Scholarship</p>
                        <p class="right">
                          LAC
                        </p>
                        <div class="clearfix"></div>
                      </div>
                      <div class="clearfix"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!--Horizontal Tab-->
        </div>
        <div class="row" style="margin-top: 30px;">
          <h3 class="last-updated"> 
            <small>Universities</small>
          </h3>
          <div class="lab-instruments" id="detail">
            <div id="parentHorizontalTab">
              <div class="resp-tabs-container hor_1">
                <div>
                  <div v-for="(item, index) in schoolList" :key="item.id">
                    <div class="col-md-3 jm-item first">
                      <div class="jm-item-wrapper">
                        <div class="jm-item-image">
                          <img :src="basePath + '/static/images/universities/' + item.shortName +'_icon.jpg'"
                               :alt="item.name">
                          <span class="jm-item-overlay"> </span>
                          <div class="jm-item-button"><a style="cursor: pointer" @click="doClick(item)">View</a></div>
                        </div>
                        <div class="jm-item-title">{{item.name}}<br></div>
                      </div>
                    </div>
                  </div>
                  <div class="clearfix"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--- /instruments ---->
  </section>
  <!-- //instruments-section -->

  <CSchool v-else :item="school"/>

</template>
<script>
  import CSchool from "../../components/School";
  import axios from 'axios'

  export default {
    components: {CSchool},
    data() {
      return {
        school: '',
        detail: false,
        schoolList: [],
        basePath: basePath
      }
    },
    mounted() {
      axios.get('/school').then(response => {
        this.schoolList = response.data;
      })
    },
    methods: {
      doClick(item) {
        let speed = 10;
        let timer = setInterval(function () {
          this.scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
          if (this.scrollTop > 0) {
            this.scrollTop = (this.scrollTop - speed > 0) ? (this.scrollTop - speed) : 0;
            speed += 20;
            window.scrollTo(0, this.scrollTop);
          } else {
            clearInterval(timer);
          }
        }, 16)
        this.school = item
        this.detail = true
      }
    }
  }
</script>



