<template>
  <section class="contact">
    <div class="inner-banner demo-2 text-center">
      <div id="breadcrumb_wrapper">
        <div class="container">
          <h2>Contact Information</h2>
          <h6></h6>
        </div>
      </div>
    </div>
    <!--contact-->
    <div class="contact">
      <div class="container">
        <div class="map">
          <h4> Map Information</h4>
          <div id="map">
            <div style="width:100%;height:400px;border:#ccc solid 1px;" id="dituContent"></div>
          </div>
        </div>
        <div class="address">
          <div class="row">
            <div class="contact-infom">
              <h4> CONTACT INFO</h4>
            </div>
          </div>
          <div class="row">
            <div class="col-md-4 location">
              <hr/>
              <h4></h4>
              <img style="width: 200px; height: 200px;" :src="basePath + '/static/images/weChat.jpg'"/>
            </div>

            <div class="col-md-4 location">
              <hr/>
              <h4> :</h4>
              <p><span class="glyphicon glyphicon-map-marker" aria-hidden="true"></span>8 </p>
              <p><span class="glyphicon glyphicon-earphone" aria-hidden="true"></span>+028 83964698</p>
              <p><span class="glyphicon glyphicon-phone" aria-hidden="true"></span>17780622519</p>
              <p><span class="glyphicon glyphicon-envelope" aria-hidden="true"></span><a href="875699924@qq.com">875699924@qq.com</a>
              </p>
              <p><span><i class="fa fa-qq"></i></span><a href="//wpa.qq.com/msgrd?v=3&uin=821978672&site=qq&menu=yes"></a>
              </p>
              <p><span class="glyphicon glyphicon-exclamation-sign" aria-hidden="true"></span></p>
            </div>
            <div class="col-md-4 location">
              <hr/>
              <h4></h4>
              <p>
                (Good CharacterCritical ThinkingLife-long LearningCommunication & Collaboration)
                
                10300100
                <i>- The Entire Pioneer Team</i></p>
            </div>
            <div class="clearfix"></div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<style type="text/css">
  .iw_poi_title {
    color: #CC5522;
    font-size: 14px;
    font-weight: bold;
    overflow: hidden;
    padding-right: 13px;
    white-space: nowrap
  }

  .iw_poi_content {
    font: 12px arial, sans-serif;
    overflow: visible;
    padding-top: 4px;
    white-space: -moz-pre-wrap;
    word-wrap: break-word
  }
</style>
<script>
  export default {
    name: 'contact',
    data(){
      return{
        basePath: basePath
      }
    },
    mounted() {
      initMap();//
    }
  }

  //
  function initMap() {
    createMap();//
    setMapEvent();//
    addMapControl();//
    addMarker();//marker
  }

  //
  function createMap() {
    var map = new BMap.Map("dituContent");//
    var point = new BMap.Point(104.084059, 30.815932);//
    map.centerAndZoom(point, 18);//
    window.map = map;//map
  }

  //
  function setMapEvent() {
    map.enableDragging();//()
    map.enableDoubleClickZoom();//()
  }

  //
  function addMapControl() {
    //
    var ctrl_nav = new BMap.NavigationControl({anchor: BMAP_ANCHOR_TOP_RIGHT, type: BMAP_NAVIGATION_CONTROL_SMALL});
    map.addControl(ctrl_nav);
    //
    var ctrl_ove = new BMap.OverviewMapControl({anchor: BMAP_ANCHOR_TOP_RIGHT, isOpen: 0});
    map.addControl(ctrl_ove);
    //
    var ctrl_sca = new BMap.ScaleControl({anchor: BMAP_ANCHOR_BOTTOM_LEFT});
    map.addControl(ctrl_sca);
  }

  //
  var markerArr = [{
    title: "",
    content: "17780622519<br/>02883964698<br/>875699924@qq.com<br/>8  ",
    point: "104.084468|30.81494",
    isOpen: 0,
    icon: {w: 21, h: 21, l: 0, t: 0, x: 6, lb: 5}
  }
  ];

  //marker
  function addMarker() {
    for (var i = 0; i < markerArr.length; i++) {
      var json = markerArr[i];
      var p0 = json.point.split("|")[0];
      var p1 = json.point.split("|")[1];
      var point = new BMap.Point(p0, p1);
      var marker = new BMap.Marker(point);  // 
      var iw = createInfoWindow(i);
      var label = new BMap.Label(json.title, {"offset": new BMap.Size(json.icon.lb - json.icon.x + 10, -20)});
      marker.setLabel(label);
      map.addOverlay(marker);
      label.setStyle({
        borderColor: "#808080",
        color: "#333",
        cursor: "pointer"
      });

      (function () {
        var index = i;
        var _iw = createInfoWindow(i);
        var _marker = marker;
        _marker.openInfoWindow(_iw);
        _marker.addEventListener("click", function () {
          this.openInfoWindow(_iw);
        });
        _iw.addEventListener("open", function () {
          _marker.getLabel().hide();
        })
        _iw.addEventListener("close", function () {
          _marker.getLabel().show();
        })
        label.addEventListener("click", function () {
          _marker.openInfoWindow(_iw);
        })
        if (!!json.isOpen) {
          label.hide();
          _marker.openInfoWindow(_iw);
        }
      })()
    }
  }

  //InfoWindow
  function createInfoWindow(i) {
    var json = markerArr[i];
    var iw = new BMap.InfoWindow("<b class='iw_poi_title' title='" + json.title + "'>" + json.title + "</b><div class='iw_poi_content'>" + json.content + "</div>");
    return iw;
  }

</script>



